import React from "react";
import TagIcon from "@assets/icons/tag.svg";
import styles from "@styles/components/PostCategoryTags.module.scss";

type Props = {
	tags: { tag: string, href: string }[];
}

const PostTags: React.FC<Props> = ({ tags }) => (
	<div className={ styles.postFilters }>
		<img src={ TagIcon }/>
		{tags.map(({ tag, href }) => (
			<a key={ `post-tag-${tag}` } href={ href }>
				<span>{`# ${tag}`}</span>
			</a>
		))}
	</div>
);

export default PostTags;
