import React from "react";
import CategoryIcon from "@assets/icons/category.svg";
import styles from "@styles/components/PostListItem.module.scss";

type Props = {
	createdAt: string;
	title: string;
	href: string;
	description: string;
	category: { category: string, href: string };
	tags: { tag: string, href: string }[];
}

type CategoryAndTagsProps = {
	category: Props["category"];
	tags: Props["tags"];
}

const CategoryAndTags: React.FC<CategoryAndTagsProps> = ({ category, tags }) => (
	<div className={ styles.postFilters }>
		<img src={ CategoryIcon }/>
		<a href={ category.href }>
			<span>{category.category}</span>
		</a>
		{tags.map(({ tag, href }) => (
			<a key={ `post-tag-${tag}` } href={ href }>
				<span>{`# ${tag}`}</span>
			</a>
		))}
	</div>
);

const PostListItem: React.FC<Props> = ({
	createdAt, title, href, description, category, tags,
}) => (
	<div className={ styles.postListItem }>
		<p className={ styles.createdAt }>{createdAt}</p>
		<a href={ href }>
			<div>
				<h2 className={ styles.title }>{title}</h2>
				<p className={ styles.description }>{description}</p>
			</div>
		</a>
		<CategoryAndTags category={ category } tags={ tags }/>
	</div>
);

export default PostListItem;
