import React, { useEffect, useRef } from "react";
import CategoryIcon from "@assets/icons/category.svg";
import TagIcon from "@assets/icons/tag.svg";
import styles from "@styles/components/Navigation.module.scss";

type Props = {
	categories: { category: string, count: number, href: string }[];
	tags: { tag: string, count: number, href: string }[];
	onBlur?: () => void
}

type CategoryNavigationProps = {
	categories: Props["categories"]
}

type TagNavigationProps = {
	tags: Props["tags"]
}

export const CategoryNavigation: React.FC<CategoryNavigationProps> = ({ categories }) => (
	<nav>
		<div>
			<img src={ CategoryIcon }/>
			<span>category</span>
		</div>
		<ul>
			{categories.map(({ category, count, href }) => (
				<li key={ `category-${category}` }>
					<a href={ href }>
						<span className={ styles.itemName }>{category}</span>
						<span className={ styles.itemCount }>{count}</span>
					</a>
				</li>
			))}
		</ul>
	</nav>
);

export const TagNavigation: React.FC<TagNavigationProps> = ({ tags }) => (
	<nav>
		<div>
			<img src={ TagIcon }/>
			<span>tag</span>
		</div>
		<ul>
			{tags.map(({ tag, count, href }) => (
				<li key={ `tag-${tag}` }>
					<a href={ href }>
						<span className={ styles.itemName }>{tag}</span>
						<span className={ styles.itemCount }>{count}</span>
					</a>
				</li>
			))}
		</ul>
	</nav>
);


export const NavigationBox: React.FC<Props> = ({ categories, tags, onBlur }) => {
	const ref = useRef(null);

	useEffect(() => {
		const handleBlur = (e: MouseEvent) => {
			if (!ref.current?.contains(e.target)) {
				onBlur && onBlur();
			}
		};
		document.addEventListener("click", handleBlur);
		return () => {
			document.removeEventListener("click", handleBlur);
		};
	}, []);

	return (
		<div className={ styles.navigationBox } ref={ ref }>
			<CategoryNavigation categories={ categories }/>
			<TagNavigation tags={ tags }/>
		</div>
	);
};

