import React from "react";
import { Helmet } from "react-helmet";
import Logo from "@assets/images/logo.svg";

type Props = {
	title?: string
	description?: string
	keywords?: string[]
}

const defaults = {
	title: "jebae's dev blog",
	description: "Programming, Math, Projects",
	keywords: ["graphics", "linear algebra", "그래픽스", "선형대수학", "개발", "프로그래밍"]
};

const Head: React.FC<Props> = ({ title, description, keywords }) => (
	<Helmet>
		<title>{title || defaults.title}</title>
		<meta name="description" content={ description || defaults.description }/>
		<meta name="keywords" content={ keywords?.join(",") || defaults.keywords.join(",") }/>
		<meta property="og:title" content={ title || defaults.title }/>
		<meta property="og:image" content={ Logo }/>
		<meta property="og:description" content={ description || defaults.description }/>
		<link rel="icon" href={ Logo }/>
		<link rel="mask-icon" href={ Logo } color="#000000"/>
	</Helmet>
);

export default Head;
