import React from "react";
import CategoryIcon from "@assets/icons/category.svg";
import styles from "@styles/components/PostCategoryTags.module.scss";

type Props = {
	category: string;
	href: string;
}

const PostCategory: React.FC<Props> = ({ category, href }) => (
	<div className={ styles.postFilters }>
		<img src={ CategoryIcon }/>
		<a href={ href }>
			<span>{category}</span>
		</a>
	</div>
);

export default PostCategory;
