import React, { useState } from "react";
import Logo from "@assets/images/logo.svg";
import styles from "@styles/components/Header.module.scss";
import CategoryIcon from "@assets/icons/category.svg";
import { NavigationBox } from "./Navigation";

type Props = {
	categories: { category: string, count: number, href: string }[];
	tags: { tag: string, count: number, href: string }[];
}

type NavProps = {
	onClickNav: () => void;
}

const Title: React.FC = () => (
	<a href="/">
		<div className={ styles.title } >
			<img src={ Logo }/>
			<p>{"jebae's dev blog"}</p>
		</div>
	</a>
);

const Nav: React.FC<NavProps> = ({ onClickNav }) => (
	<nav className={ styles.nav }>
		<ul>
			<li onClick={ onClickNav }>
				<img src={ CategoryIcon }/>
			</li>
		</ul>
	</nav>
);

const Header: React.FC<Props> = ({ categories, tags }) => {
	const [showNav, setShowNav] = useState(false);

	return (
		<header className={ styles.header }>
			<div className={ styles.container }>
				<Title/>
				<Nav onClickNav={ () => setShowNav(true) }/>
			</div>
			{showNav &&
			<section className={ styles.navigation }>
				<NavigationBox
					categories={ categories }
					tags={ tags }
					onBlur={ () => setShowNav(false) }
				/>
			</section>}
		</header>
	);
};

export default Header;
